import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { graphql, useStaticQuery} from "gatsby"
import PopularList from "../components/PopularList"
import Typography from "@material-ui/core/Typography"
import Box from "@material-ui/core/Box"
import makeStyles from "@material-ui/core/styles/makeStyles"
import Grid from "@material-ui/core/Grid"
import Img from "gatsby-image"
import CategoryList from "../components/CategoryList";
import SocialList from "../components/SocialList";

const useStyles = makeStyles(theme => ({
  siteInfo: {
    minHeight: "calc(100vh - 48px)",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
  },
  siteSocial: {
    flexGrow: 0,
  },
  categoriesPhoto: {
    flexGrow: 1,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  text: {
    color: theme.palette.text.primary,
  },
}))

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    query {
      allContentfulProduct {
        nodes {
          contentful_id
          price
          newPrice
          spaceId
          name
          description {
            description
          }
          photo {
            fluid {
              ...GatsbyContentfulFluid_tracedSVG
            }
          }
        }
      }
      contentfulImage(title: { eq: "backgroundFirstSection" }) {
        image {
          fluid {
            ...GatsbyContentfulFluid_tracedSVG
          }
        }
      }
      contentfulRichText(title: { eq: "О магазине (текст)" }) {
        text {
          json
        }
      }
    }
  `)
  const products = data.allContentfulProduct.nodes
  const image = data.contentfulImage.image.fluid

  const classes = useStyles()
  return (
    <Layout>
      <SEO />
      <Typography variant="srOnly" component="h1">
        Строймаркет
      </Typography>
      <Box component="section" className={classes.siteInfo}>

          <SocialList/>
          <Grid container spacing={3} className={classes.categoriesPhoto} >
            <Grid item xs={12} md={6}>
              <Box>
                <Typography
                  variant="h4"
                  component="h2"
                  align="left"
                  color="textPrimary"
                >
                  Привет! В нашем магазине вы найдете:
                </Typography>
                <CategoryList/>
              </Box>

            </Grid>
            <Grid item xs={12} md={6} container alignItems="top" justify={"center"}>
              <Grid xs={12} >
                <Img fluid={image} />
              </Grid>
            </Grid>
          </Grid>
      </Box>
      <section>
        <Typography variant="h4" component="h2" color="textPrimary">
          Хиты продаж
        </Typography>
        <PopularList products={products} />
      </section>
    </Layout>
  )
}

export default IndexPage
