import React from "react"
import { makeStyles } from "@material-ui/core/styles"
import Typography from "@material-ui/core/Typography"
import Box from "@material-ui/core/Box"
const useStyles = makeStyles (theme=>({
  discount: {
    position: "absolute",
    zIndex: 1,
    transform: "rotate(-45deg) translate(-25px, -40px)",
    backgroundColor: "#fa9155",
    width: 140,
    height: 65,
    display: "flex",
    justifyContent: "center",
    alignItems: "flex-end",
  }}));

const Discount = ({ newPrice})=>{
  const classes = useStyles();
  const discountString = `-15%`
  return newPrice ? <Box className={classes.discount}>
    <Typography variant="h6" component="strong" color="textPrimary">
      {discountString}
    </Typography>
  </Box> : null;
}
export default Discount;
