import React from "react"
import { makeStyles } from "@material-ui/core/styles"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Typography from "@material-ui/core/Typography"
import Img from "gatsby-image"
import Box from "@material-ui/core/Box"
import { graphql, navigate, useStaticQuery } from "gatsby"
import { slugify } from "transliteration"
import Discount from "./Discount"
const useStyles = makeStyles({
  root: {
    height: "100%",
    position: "relative",
    overflow: "hidden",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  cardContent: {
    height: "100%",
  },
  box: {
    display: "flex",
    flexDirection: "column",
    height: "100%",
  },
  info: {
    display: "flex",
    flexDirection: "column",
    flexGrow: 1,
    justifyContent: "space-between"
  },
  infoImage: {
    "&:hover": {
      cursor: "pointer",
    },
  },
})

export default function Product({ product }) {
  const classes = useStyles()
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "no-photo.jpg" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  const noImage = data.file.childImageSharp.fluid
  return (
    <Card className={classes.root} variant="outlined">
      <Discount price={product.price} newPrice={product.newPrice} />
      <CardContent className={classes.cardContent}>
        <Box className={classes.box}>
          <Box className={classes.info}>
            <Box
              className={classes.infoImage}
              onClick={() => {
                navigate(slugify(product.name))
              }}
            >
              <Img
                fluid={product.photo ? product.photo.fluid : noImage}
                style={{
                  maxHeight: "200px",
                  margin: "5px 5px 20px",
                  flexGrow: 1,
                }}
                imgStyle={{ objectFit: "contain" }}
              />
            </Box>
            <Typography variant="body1" component="h3" color="textPrimary">
              {product.name}
            </Typography>
          </Box>
        </Box>
      </CardContent>
    </Card>
  )
}
